import { useRouter } from 'next/router';
import Layout from '../../components/Layout';
import RiverList from '../../components/RiversList';
import River from '../../components/River';


export default function Post() {
    
    
    const router = useRouter();
    const name = River.name;
    const riverId = router.query.id;
    const riverReview = <div><h1>{router.query.id}</h1>
        
        if({riverId} == {name}){
             <RiverList />
        }
        else{
            
        };

        <p>This will have the river post info.</p></div>

    return (
        <Layout content={riverReview} />

    );
}
